
  .inputs-nyawxa {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
  }
  
  
  .inputs-nyawxa > input {
    border-radius: 4px;
    border: 1px solid;
    margin: 1px;
    padding: 4px 8px;
  }
  
  .inputs-nyawxa > button:nth-child(3) {
    margin-top: 8px;
    cursor: pointer;
    padding: 4px 20px;
  }
  

  .error-xuthnv {
